.yagr {
  --yagr-font-family:
    "Roboto",
    "Helvetica Neue",
    "Helvetica",
    Arial,
    sans-serif;
}
.yagr_theme_light,
.yagr_theme_light-hc {
  --yagr-title-color: #333333;
  --yagr-legend-item: rgba(0, 0, 0, 0.5);
  --yagr-legend-item-hover: #000000;
  --yagr-legend-item-hidden: rgba(0, 0, 0, 0.3);
  --yagr-background: #ffffff;
  --yagr-grid: rgba(0, 0, 0, 0.105);
  --yagr-axis-stroke: rgba(0, 0, 0, 0.5);
}
.yagr_theme_dark,
.yagr_theme_dark-hc {
  --yagr-title-color: rgba(255, 255, 255, 0.35);
  --yagr-legend-item: rgba(255, 255, 255, 0.5);
  --yagr-legend-item-hover: #a09fa3;
  --yagr-legend-item-hidden: hsla(0, 0%, 100%, 0.17);
  --yagr-background: #2d2c33;
  --yagr-grid: rgba(255, 255, 255, 0.23);
  --yagr-axis-stroke: rgba(255, 255, 255, 0.5);
}
.yagr_theme_dark-hc {
  --yagr-background: rgba(34, 35, 38, 1);
}
.yagr {
  width: 100%;
  height: 100%;
  font-family: var(--yagr-font-family);
  background-color: var(--yagr-background);
  overflow: hidden;
}
.yagr .uplot {
  width: 100%;
  font-family: var(--yagr-font-family);
}
.yagr .u-title {
  color: var(--yagr-title-color);
  font-size: 14px;
  font-weight: bold;
  line-height: 14px;
  margin-top: 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.yagr .u-select {
  background: rgba(51, 92, 173, 0.25);
}
.uplot,
.uplot *,
.uplot *::before,
.uplot *::after {
  box-sizing: border-box;
}
.uplot {
  font-family:
    system-ui,
    -apple-system,
    "Segoe UI",
    Roboto,
    "Helvetica Neue",
    Arial,
    "Noto Sans",
    sans-serif,
    "Apple Color Emoji",
    "Segoe UI Emoji",
    "Segoe UI Symbol",
    "Noto Color Emoji";
  line-height: 1.5;
  width: min-content;
}
.u-title {
  text-align: center;
  font-size: 18px;
  font-weight: bold;
}
.u-wrap {
  position: relative;
  user-select: none;
}
.u-over,
.u-under {
  position: absolute;
}
.u-under {
  overflow: hidden;
}
.uplot canvas {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
}
.u-axis {
  position: absolute;
}
.u-legend {
  font-size: 14px;
  margin: auto;
  text-align: center;
}
.u-inline {
  display: block;
}
.u-inline * {
  display: inline-block;
}
.u-inline tr {
  margin-right: 16px;
}
.u-legend th {
  font-weight: 600;
}
.u-legend th > * {
  vertical-align: middle;
  display: inline-block;
}
.u-legend .u-marker {
  width: 1em;
  height: 1em;
  margin-right: 4px;
  background-clip: padding-box !important;
}
.u-inline.u-live th::after {
  content: ":";
  vertical-align: middle;
}
.u-inline:not(.u-live) .u-value {
  display: none;
}
.u-series > * {
  padding: 4px;
}
.u-series th {
  cursor: pointer;
}
.u-legend .u-off > * {
  opacity: 0.3;
}
.u-select {
  background: rgba(0, 0, 0, 0.07);
  position: absolute;
  pointer-events: none;
}
.u-cursor-x,
.u-cursor-y {
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
  will-change: transform;
}
.u-hz .u-cursor-x,
.u-vt .u-cursor-y {
  height: 100%;
  border-right: 1px dashed #607D8B;
}
.u-hz .u-cursor-y,
.u-vt .u-cursor-x {
  width: 100%;
  border-bottom: 1px dashed #607D8B;
}
.u-cursor-pt {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  border: 0 solid;
  pointer-events: none;
  will-change: transform;
  background-clip: padding-box !important;
}
.u-axis.u-off,
.u-select.u-off,
.u-cursor-x.u-off,
.u-cursor-y.u-off,
.u-cursor-pt.u-off {
  display: none;
}
.yagr-tooltip {
  --yagr-tooltip-font-family:
    "Lucida Grande",
    "Lucida Sans Unicode",
    Arial,
    Helvetica,
    sans-serif;
  position: absolute;
  z-index: 1000;
  background-color: #fff;
  border-radius: 4px;
  overflow: hidden;
  font-family: var(--yagr-tooltip-font-family);
  pointer-events: none;
}
.yagr-tooltip__item {
  white-space: nowrap;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}
.yagr-tooltip__item._active {
  font-weight: bold;
}
.yagr-tooltip_default {
  font-size: 12px;
  padding: 4px 12px;
  box-shadow: 2px 0px 3px rgba(0, 0, 0, 0.1);
}
.yagr-tooltip_default .__title {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 4px;
}
.yagr-tooltip_default .__section {
  margin-bottom: 4px;
}
.yagr-tooltip_default .__section:last-child {
  margin: 0;
}
.yagr-tooltip_default .__section_title {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 2px;
}
.yagr-tooltip_default .__section_scale {
  margin-bottom: 2px;
  background-color: #eee;
  padding: 2px;
}
.yagr-tooltip_default .yagr-tooltip__mark {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 8px;
  display: block;
}
.yagr-tooltip_pinned {
  pointer-events: all;
}
.yagr-tooltip_pinned ._tooltip-list {
  max-height: 320px;
}
.yagr-tooltip ._hidden-rows-number {
  white-space: nowrap;
}
.yagr-tooltip__idx {
  width: 25px;
  white-space: nowrap;
  max-width: 25px;
  overflow: hidden;
}
.yagr-tooltip__tf {
  margin-left: 4px;
}
.yagr-point {
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
  will-change: transform;
  z-index: 100;
}
.yagr-point span {
  border-radius: 50%;
  display: block;
  width: 100%;
  height: 100%;
  border: solid 1px #fff;
  box-shadow: 0 1px 1px -1px #fff;
}
.yagr-legend {
  padding: 8px 24px 12px;
  display: block;
  max-width: 100%;
  width: 100%;
}
.yagr-legend__top {
  padding: 8px 24px 0;
}
.yagr-legend__container {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.yagr-legend__items {
  display: flex;
  flex-flow: row wrap;
  padding-left: 16px;
}
.yagr-legend__item {
  cursor: pointer;
  max-width: 350px;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-right: 16px;
  color: var(--yagr-legend-item);
  font-weight: normal;
  position: relative;
  white-space: nowrap;
  max-height: 14px;
  font-size: 12px;
  line-height: 14px;
}
.yagr-legend__item:last-child {
  margin-right: 0;
}
.yagr-legend__item:hover {
  color: var(--yagr-legend-item-hover);
}
.yagr-legend__item_hidden {
  color: var(--yagr-legend-item-hidden);
}
.yagr-legend__item_hidden .yagr-legend__icon {
  background-color: var(--yagr-legend-item-hidden) !important;
}
.yagr-legend__icon {
  margin-right: 4px;
  width: 12px;
  display: inline-block;
  position: relative;
}
.yagr-legend__icon_area,
.yagr-legend__icon_column,
.yagr-legend__icon_dots {
  height: 12px;
  border-radius: 50%;
  top: 2px;
}
.yagr-legend__icon_line {
  height: 3px;
  top: -2px;
}
.yagr-legend__pagination {
  padding-left: 14px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.yagr-legend__pagination-text {
  user-select: none;
  color: var(--yagr-legend-item-hover);
  font-size: 12px;
}
.yagr-legend__icon-up {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 6px 12px;
  border-color: transparent transparent #003399;
  cursor: pointer;
}
.yagr-legend__icon-up_disabled {
  cursor: initial;
  border-color: transparent transparent #cccccc;
}
.yagr-legend__icon-down {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 12px 6px 0;
  border-color: #003399 transparent transparent;
  cursor: pointer;
}
.yagr-legend__icon-down_disabled {
  cursor: initial;
  border-color: #cccccc transparent transparent;
}
